
.maincard{
  margin-top: 2rem;
  margin-bottom: 2rem;
  margin-left: 3rem;
    border-radius: 5%;
    width: 100%;
    background-color: #181717;
    border-color: white;
    border-style: solid;
    border-radius: 5rem;
    box-shadow: 5px 5px 10px  whitesmoke;
    position: relative;
}

.line {
    position: relative;
    max-width: 80%;
    margin: 0 0 0 10%;
  }

  .line::after {
    content: "";
    position: absolute;
    width: 3px;
    background-color: #fff;
    top: 0;
    bottom: 0;
    margin-left: -3px;
  }

  .Component {
    position: relative;
    background-color: inherit;
    width: 100%;
  }
  .Component::after {
    content: "";
    position: absolute;
    width: 17px;
    height: 17px;
    background-color: #fff;
    top: 18px;
    border-radius: 50%;
    z-index: 1;
  }
  .right-5 {
    padding: 0px 0px 20px 4px;
    left: auto;
  }
  
  
  /* Fix the circle for containers on the right side */
  .right-5::after {
    left: -10px;
  }
  .p-3{
    color: white;
  }

  .content{
    margin-left: 7%;
    color: rgb(207, 193, 193);
  }
.row{
  width: 98%;
}



.back-shapes {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  margin-left: 2rem;

}

.back-shapes img {
  opacity: 0.2;
  position: absolute;
  width: 1.5%;
}

.floating {
  position: absolute;
  -webkit-animation-name: floating;
  animation-name: floating;
  -webkit-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
 
}
.floating.circle {
  display: inline-block;
  width: 39px;
  height: 39px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.floating.square {
  display: inline-block;
  width: 35px;
  height: 35px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

}
.floating.triangle {
  display: inline-block;
  width: 35px;
  height: 35px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.floating.cross {
  display: inline-block;
  width: 35px;
  height: 35px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

@keyframes floating {
  0% {
    transform: translateY(0%) rotate(-55deg);
  }
  50% {
    transform: translateY(300%) rotate(55deg);
  }
  100% {
    transform: translateY(0%) rotate(-55deg);
  }
}
@-webkit-keyframes floating {
  0% {
    -webkit-transform: translateY(0%);
  }
  50% {
    -webkit-transform: translateY(300%);
  }
  100% {
    -webkit-transform: translateY(0%);
  }
}